import React, { Component } from 'react';
import FaceDetection from './faceDetect' 
import './App.css';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Navbar from './components/Navbar/navbar'
import Home from './components/Home/home'
 

//=============================

class App extends React.Component {
  constructor() {
    super();
  }

  render() {
    return (
      <React.Fragment>
        <BrowserRouter>
          <div className="app">
              <Navbar/>
              <div>
                <Switch>
                <Route exact path="/" component={Home}></Route>
                <Route exact path="/app" component={FaceDetection}></Route>
                
                </Switch>  
               </div> 
             
          </div>
          
        </BrowserRouter>
      </React.Fragment>
    );
  }
}

export default App;


